<template>
  <div class="box">
    <img src="../assets/4-2.png" alt="" class="chengji" />
    <div class="fs">
      <div>{{ num }}分！</div>
      <div v-if="num==100">你也太聪明了，葡萄之家欢迎您！</div>
      <div v-else-if="num>=80">智商离爆表只差一步！</div>
      <div v-else-if="num>=60">不要得瑟，还有进步空间！</div>
      <div v-else-if="num>=40">还不错，但还需要加油哦！</div>
      <div v-else>你说你是不是把知识都还给小学老师了！</div>
    </div>
    <img src="../assets/4-3.png" alt="" class="share" @click="share" />
    <div class="text">关注葡萄之家,获取答案</div>
    <img src="../assets/4-4.png" alt="" class="er" />
    <div :class="{ bg: true, show: show }" @click="fn"></div>
    <img
      src="../assets/5-2.png"
      alt=""
      :class="{ toshare: true, show: show }"
    />
    <div class="time">用时{{time}}s</div>
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      show: true,
    };
  },
  methods: {
    share() {
      this.show = false;
    },
    fn() {
      this.show = true;
    },
  },
  components: {},
  computed: {
    num() {
      return this.$store.state.right * 20;
    },
    time(){
      return Math.round((this.$store.state.secTime - this.$store.state.firTime)/1000)
       
    }
  },
};
</script>

